body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* http://alistapart.com/article/now-you-see-me */
.blur-up__hidden {
  position: absolute;
  left: -999em;
}

.blur-up__blur {
  -webkit-filter: blur(15px);
          filter: blur(15px);
}

.blur-up__animation {
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-transition-property: opacity, visibility;
  -o-transition-property: opacity, visibility;
  transition-property: opacity, visibility;

  visibility: visible;
  opacity: 1;
}

.blur-up__animation--fade-out {
  visibility: hidden;
  opacity: 0;
}

/* http://tachyons.io/components/layout/horizontal-aspect-ratios/index.html */
.blur-up__aspect-ratio {
  position: relative;
  overflow: hidden;
}

.blur-up__aspect-ratio--16x9 {
  padding-bottom: 56.25%;
}

.blur-up__aspect-ratio--4x3 {
  padding-bottom: 75%;
}

.blur-up__aspect-ratio--6x4 {
  padding-bottom: 66.6%;
}

.blur-up__aspect-ratio--8x5 {
  padding-bottom: 62.5%;
}

.blur-up__aspect-ratio--7x5 {
  padding-bottom: 71.42%;
}

.blur-up__aspect-ratio--1x1 {
  padding-bottom: 100%;
}

.blur-up__aspect-ratio--object {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  width: 100%;
  height: 100%;

  background-position: 50% 50%;
  background-size: cover;
}

.fixed-bg {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  z-index: 0;
}


